angular.module("Pingce").controller("toBeDistributorController", function($scope, $timeout, $location, userServices, errorServices, toastServices, localStorageService, config) {
	$scope.input = {};
	$scope.provinces = ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""]
	$scope.input.province = $scope.provinces[0];
	$scope.query_cities = function() {
		userServices.query_cities().then(function(data) {
			$scope.cities = data[$scope.input.province];
			$scope.input.city = $scope.cities[0];
		})
	}
	$scope.$watch("input.province", function(n, o) {
		if (n) {
			$scope.query_cities();
		}
	});
	$scope.ajaxForm = function() {
		toastServices.show();
		userServices.apply_distributor({
			"name": $scope.input.name,
			"telephone": $scope.input.telephone,
			"province": $scope.input.province,
			"city": $scope.input.city,
			"brand": $scope.input.brand,
			"mall": $scope.input.mall,
		}).then(function(data) {
			toastServices.hide()
			if (data.code == config.request.SUCCESS && data.status == config.response.SUCCESS) {
				errorServices.autoHide(data.message);
				$timeout(function() {
					$location.path("index").replace();
				}, 2000)
			} else {
				errorServices.autoHide(data.message);
			}
		})
	}
})