angular.module("Pingce").controller("singleCommentController", function($scope, userServices, $routeParams, errorServices, toastServices, localStorageService, config) {
	$scope.comments = [];
	$scope.page = {
		pn: 1,
		page_size: 10,
		message: "",
		find_jiaju_brand_series_rooms_blank_single_id: $routeParams.id
	}
	$scope.loadMore = function() {
		if ($scope.no_more) {
			return;
		}
		toastServices.show();
		$scope.page.message = "...";
		userServices.query_single_comment_by_id($scope.page).then(function(data) {
			toastServices.hide();
			$scope.page.message = "";
			if (data.code == config.request.SUCCESS && data.status == config.response.SUCCESS) {
				$scope.comments = $scope.comments.concat(data.Result.CommentList.list);
				$scope.serial = data.Result.FBlankSingleDetail;
				$scope.no_more = $scope.comments.length == data.Result.CommentList.totalRow ? true : false;
			} else {
				errorServices.autoHide("");
			}
			if ($scope.no_more) {
				$scope.page.message = "";
			}
			$scope.page.pn++;
		})

	}
	$scope.loadMore();
	$scope.buy_aim = ["", "", "", "", "", ""];
	$scope.parse_aim = function(a) {
		return $scope.buy_aim[a];
	}
})