angular.module("Pingce").directive('duoshuoComment', function($timeout) {
	return {
		restrict: 'E',
		scope: {
			key: "@",
			title: "@",
			url: "@"
		},
		link: function(scope, element, attrs) {
			$timeout(function() {
				var el = document.createElement('div'); //divclass="ds-thread"
				el.setAttribute('data-thread-key', scope.key); //
				el.setAttribute('data-url', scope.url); //
				el.setAttribute('data-title', scope.title); //
				DUOSHUO.EmbedThread(el);
				$(element).append(el);
			}, 2000)
		}
	};
});