angular.module("Pingce").directive('mobilepicker', function($timeout) {
	return {
		restrict: 'E',
		scope: {
			options: "=",
		},
		template: "<input type='text' name='picker' readonly ng-model='options.value'>",
		link: function(scope, element, attrs) {
			var options = {
				// (2010)
				beginyear: 1950,
				// 2020
				endyear: new Date().getFullYear(),
				// ['','','','','','','']
				days: ['', '', '', '', '', '', ''],
				// 
				// beginhour: "",
				// 
				// endhour: "",
				// 
				// beginminute: "",
				// 
				// endminute: "",
				// ,true,falseinputvalue,false
				curdate: false,
				// theme 
				// date:
				// datetime:
				// time:
				// month:
				theme: "date",
				// ,click
				event: "click",
				// iscroll4,iscroll4
				// scrollOpt: {},
				// function(){}
				callBack: function(date) {
					scope.options.value = date.y + "-" + date.M + "-" + date.d
				}
			};
			// function body
			$timeout(function() {
				$(element).find("input").datePicker(angular.extend({}, options, scope.options));
			}, 0)
		}
	};
});